html,*::before,*::after{box-sizing:border-box}body{margin:0;padding:20px 40px;font-family:'Open Sans Condensed', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}@media screen and (max-width: 800px){body{padding:10px}}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}a{text-decoration:none;color:black}

.Error{text-align:center}.Error h1{margin:0;background:whitesmoke;position:absolute;font-size:57px;font-weight:bolder;color:white;left:50%;transform:translateX(-50%)}

